.card {
  background: #fff;
  border-radius: 2px;
  height: 250px;
  margin: 1rem;
  position: relative;
  width: 325px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: -webkit-gradient(linear, left top, right top, from(#a4f096), to(#c7fbf6));
  background: -webkit-linear-gradient(left, #a4f096, #c7fbf6);
  background: -o-linear-gradient(left, #a4f096, #c7fbf6);
  background: linear-gradient(to right, #a4f096, #c7fbf6);
}
.card > .img-container > img {
  margin-top: 20px;
  width: 200px;
  height: 200px;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}









